.skillsWrapper {
  margin: 0;
  padding: 10vh 0 10vh 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  perspective: 350px;
  perspective-origin: 50% 50%;
}

.skillsmain {
  justify-content: center;
  align-items: center;
  display: grid;
  grid-template-columns: 1.3fr 1.3fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.skillscontainer {
  width: 150px;
  height: 150px;
  margin: 15px;
  transform-style: flat;
}

.skillscontainer > div {
  position: absolute;
  width: 150px;
  height: 150px;
  background: grey;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  will-change: transform;
  cursor: pointer;
}
